





























































































































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator';
import jQuery from 'jQuery';

@Component({
  components: {
    // SubMenu,
  },
})
export default class ProjectContent extends Vue {
  private isDisplay: boolean = false;
  private isPlaying: boolean = false;

  private mounted() {
    this.fadein();
    $('.content').scroll(() => {
      this.fadein();
      this.stop();
    });
  }

  private fadein(): void {
    const offset = - 60;
    const effectPos = $(window).height()! - offset;
    $('.column').each( function() {
      const thisPos = $(this).offset()!.top;
      if ( effectPos > thisPos ) {
        $(this).css({
            'opacity': 1,
            'transform': 'translate(0px, 0px)',
            '-ms-filter': 'blur(0px)',
            'filter': 'blur(0px)',
        });
      } else {
        $(this).css({
            'opacity': 0,
            'transition-delay': '0s',
        });
      }
    });
  }

  private stop(): void {
    if (this.isPlaying) {
      this.isPlaying = false;
      return;
    }

    document.querySelector('video')!.pause();
    this.isDisplay = false;

    $('.content, #sub-menu').css({
      opacity: 1,
    });

    $('video').css({
      'opacity': 0,
      'z-index': -10,
    });
  }

  private created() {
    setTimeout(() => {
      $('.content').css('top', '60px');
    }, 1000);
  }

  private play(event: any): void {
    if ( this.isDisplay) {
      this.stop();
      return;
    }

    this.isDisplay = true;
    this.isPlaying = true;

    $('.content, #sub-menu').css({
      opacity: 0,
    });

    $('video').css({
      'opacity': 1,
      'z-index': 10,
    });
  }
}
